.menu-new-wrapper {
  padding-top: 3px;
  padding-left: 44px;
}

.menu-news {
  display: flex;
  gap:20px;
}
.menu-news a {
  text-decoration: none;
}
.first-News {
  border-radius: 30px;
  background: #f7f8fa;
  width: 199px;
  height: 261px;
  /* width: 100%; */
  /* padding-top: 27px; */
  padding: 15px;
}

.first-news-top-heading {
  color: #29292b;
  font-feature-settings: "liga" off, "clig" off;
  font-family: "sequel_sans_semi_bold_disp" !important;
  font-size: 20px;
  font-style: normal;
  font-weight: 420;
}

.first-News {
  width: 199px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* padding-top: 27px;
  padding-left: 15px; */
  padding: 15px;
  padding-top: 17px;
  height: 261px;
}
.first-news-sub-heading {
  color: #29292b;
  font-feature-settings: "liga" off, "clig" off;
  font-family: "sequel_sans_semi_bold_disp" !important;
  font-size: 26px;
  font-style: normal;
  font-weight: 420;
  padding: 0;
}
.first-news-description {
  color: #29292b;
  font-family: "sequel_sans_roman_head" !important;
  font-size: 12px;
  font-style: normal;
  font-weight: 360;
  line-height: 120%; /* 14.4px */
  letter-spacing: -0.12px;
  margin-top: 20px;
  width: 168px;
}

.first-news-button {
  /* margin-top: 89px; */
  border-radius: 60px;
  border: 1px solid #29292b;
  width: 168px;
  height: 42px;
  flex-shrink: 0;
  background: transparent;
  color: #29292b;

  text-align: center;
  font-family: "sequel_sans" !important;
  font-size: 16px;
  font-style: normal;
  font-weight: 415;
  line-height: normal;
  /* margin-bottom: 15px; */
}

.first-news-button-3{
    /* margin-top: 77px; */
    border-radius: 60px;
    border: 1px solid #29292b;
    width: 168px;
    height: 42px;
    flex-shrink: 0;
