a.menuActive {
  width: 199px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* padding-top: 27px;
  padding-left: 15px; */
  padding: 15px;
  padding-top: 17px;
  height: 261px;
  background-image: url("https://memate-website.s3.ap-southeast-2.amazonaws.com/assets/about-1.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.menu-news a:hover {
  width: 199px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* padding-top: 27px;
  padding-left: 15px; */
  padding: 15px;
  padding-top: 17px;
  height: 261px;
  background-image: url("https://memate-website.s3.ap-southeast-2.amazonaws.com/assets/about-1.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
a.menuActive span.first-news-top-heading,a.menuActive .first-news-sub-heading,a.menuActive p.first-news-description{
  color: #fff;
}
a.menuActive button {
  border-color: #fff;
  color: #fff;
}