.mainMenuPages{
    .mainHeadTitle{
        text-align: center;
        padding: 30px 0 120px;
        h1{
           
            background: linear-gradient(to right, #1ab2ff 33.15%, #ffb258 62%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-family: "sequel_sans_bold_disp" !important;
            margin: 0px;
            text-align: center;
            font-size: 220px;
            font-style: normal;
            font-weight: 700;
            line-height: 230px; 
            
        }
        p{
            color: #757B8A;
            text-align: center;
            font-size: 18px;
            font-style: normal;
            font-weight: 360;
            line-height: 140%; 
        }
    }
    .mainGridWrap{
        width: 1200px;
        margin: 0 auto 92px;
        h2{
            color: #29292B;
            font-size: 32px;
            font-style: normal;
            font-weight: 600;
            line-height: normal;
            margin-bottom: 24px;
        }
        .mainGridwtapFlex{
            .itemFlex{
                display: flex;
                align-items: flex-start;
                justify-content: flex-start;
                border-radius: 30px;
                background: #F7F8FA;
                width: 383px;
                padding: var(--spacing-3xl, 24px);
                justify-content: center;
                align-items: flex-start;
                gap: var(--spacing-md, 16px);
                cursor: pointer;
                position: relative;
                
                &::before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    border-radius: 30px;
                    padding: 1px;
                    background: linear-gradient(#24B3F8, #FFB258);
                    mask: linear-gradient(#fff 0 0) content-box, 
                        linear-gradient(#fff 0 0);
                    mask-composite: exclude;
                    -webkit-mask-composite: destination-out;
                    z-index: -1;
                    opacity: 0;
                    transition: opacity 0.3s ease;
                }
                // &:hover::before {
                //     opacity: 1; 
                //     z-index: 9;
                //     border-radius: 30px;
                   
                //   }
                &:hover {
                    box-shadow: 0px 12px 16px -4px rgba(16, 24, 40, 0.08), 0px 4px 6px -2px rgba(16, 24, 40, 0.03);
                   
                  }
                .itemicon{
                    margin-top: 7px;
                    position: relative;
                    // .withoutHover{
                    //     position: absolute;
                    // }
                   
                }
                p{
                    color: #888E9E;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: 360;
                    line-height: 140%; 
                    max-height: 100px;
                    overflow-y: auto;
                    padding-right: 1px;
                    &::-webkit-scrollbar 
                  
                    {
                     width: 4px;
                   }
                   
                  &::-webkit-scrollbar-track 
                   {
                     box-shadow: inset 0 0 3px #bebfc0; 
                     border-radius: 0px;
                   }
                   &::-webkit-scrollbar-thumb 
                   {
                     background: linear-gradient(290deg, rgba(244,178,97,1) 0%, rgba(26,178,255,1) 100%);
                     border-radius: 10px;
                   }
                   }          
             }
        }
        .mainGridwtapFlex{
            column-gap: 15px;
            display: grid;
            grid-template-columns: 33.33% 33.33% 33.33%;
            row-gap: 15px;
        }
    }
}



.ResourcesPages{
    .itemFlex{
        justify-content: flex-start !important;
    }
}

  .searchFilterList{
    width: 768px;
    padding: var(--spacing-3xl, 24px) var(--spacing-4xl, 32px);
    display: flex;
    align-items: center;
    gap: var(--spacing-md, 8px);
    margin: 60px auto 0px;
    border-radius: 60px;
    border: 1px solid var(--Border, #D0D5DD);
    background: var(--Base-White, #FFF);
    text-align: left;
       input{
        color: var(--Gray-400, #98A2B3);
        font-size: 32px;
        font-style: normal;
        font-weight: 405;
        line-height: 24px; 
        width: 100%;
        border: 0;
       }
  }