  .switch {
 
        position: relative;
        display: flex;
        position: relative;
        display: flex;
        width: 286px;
        height: 62px;
        align-items: center;
        justify-content: space-between;
        margin: 0 auto 16px;
        border-bottom: 60px;
        padding: 0 31px;
    
  }
  
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 60px;
    border: 1px solid var(--Gray-300, #D0D5DD);
    transition: 0.4s;
  }
  
  .slider:before {
   
        position: absolute;
        content: "";
        height: 62px;
        width: 50%;
        left: 0;
        bottom: 0;
        transition: 0.4s;
        border-radius: 60px;
        background: linear-gradient(to right, #1ab2ff 10.15%, #ffb258 69%);
    
  }
  
  input:checked + .slider {
  
  }
  
  input:checked + .slider:before {
    transform: translateX(100%);
  }
  
